import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { 
  Music, Heart, Share2, Download, Play, Pause, Volume2, 
  Calendar, User, Clock, ArrowLeft, ExternalLink, Copy,
  MoreVertical, Edit3, Trash2
} from 'lucide-react';
import { useUser } from '@clerk/clerk-react';
import api from '../utils/api';

const PlaylistDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useUser();
  
  const [playlist, setPlaylist] = useState(null);
  const [loading, setLoading] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTrack, setCurrentTrack] = useState(null);
  const [showShareModal, setShowShareModal] = useState(false);
  const [showMenu, setShowMenu] = useState(false);

  useEffect(() => {
    fetchPlaylist();
  }, [id]); // eslint-disable-line react-hooks/exhaustive-deps

  const fetchPlaylist = async () => {
    try {
      setLoading(true);
      const response = await api.get(`/api/playlists/${id}`);
      setPlaylist(response.data);
    } catch (error) {
      console.error('Error fetching playlist:', error);
      navigate('/404');
    } finally {
      setLoading(false);
    }
  };

  const likePlaylist = async () => {
    try {
      await api.post(`/api/playlists/${id}/like`);
      setPlaylist(prev => ({
        ...prev,
        userLiked: !prev.userLiked,
        likes: prev.userLiked 
          ? prev.likes?.filter(like => like !== user?.id) || []
          : [...(prev.likes || []), user?.id]
      }));
    } catch (error) {
      console.error('Error liking playlist:', error);
    }
  };

  const deletePlaylist = async () => {
    if (!window.confirm('Are you sure you want to delete this playlist?')) return;
    
    try {
      await api.delete(`/api/playlists/${id}`);
      navigate('/my-playlists');
    } catch (error) {
      console.error('Error deleting playlist:', error);
      alert('Failed to delete playlist');
    }
  };

  const togglePublic = async () => {
    try {
      await api.patch(`/api/playlists/${id}`, {
        isPublic: !playlist.isPublic
      });
      setPlaylist(prev => ({ ...prev, isPublic: !prev.isPublic }));
    } catch (error) {
      console.error('Error updating playlist:', error);
      alert('Failed to update playlist');
    }
  };

  const playTrack = (track) => {
    setCurrentTrack(track);
    setIsPlaying(true);
    // In a real app, you'd integrate with Spotify Web Playback SDK
  };

  const copyShareLink = () => {
    const url = window.location.href;
    navigator.clipboard.writeText(url);
    alert('Link copied to clipboard!');
  };

  const getTotalDuration = () => {
    if (!playlist?.tracks) return '0:00';
    const totalMs = playlist.tracks.reduce((total, track) => total + (track.duration_ms || 0), 0);
    const minutes = Math.floor(totalMs / 60000);
    const hours = Math.floor(minutes / 60);
    return hours > 0 ? `${hours}h ${minutes % 60}m` : `${minutes}m`;
  };

  const isOwner = playlist && user && playlist.creator?.clerkId === user.id;

  if (loading) {
    return (
      <div className="max-w-6xl mx-auto">
        <div className="animate-pulse">
          <div className="bg-gray-200 h-64 rounded-xl mb-8"></div>
          <div className="space-y-4">
            {[...Array(10)].map((_, i) => (
              <div key={i} className="bg-gray-200 h-16 rounded-lg"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (!playlist) {
    return (
      <div className="max-w-6xl mx-auto text-center py-16">
        <h1 className="text-2xl font-bold text-gray-900 mb-2">Playlist not found</h1>
        <p className="text-gray-600 mb-6">The playlist you're looking for doesn't exist or has been deleted.</p>
        <button
          onClick={() => navigate(-1)}
          className="px-6 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
        >
          Go Back
        </button>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto">
      {/* Back Button */}
      <button
        onClick={() => navigate(-1)}
        className="flex items-center space-x-2 text-gray-600 hover:text-gray-900 mb-6 transition-colors"
      >
        <ArrowLeft className="w-5 h-5" />
        <span>Back</span>
      </button>

      {/* Playlist Header */}
      <div className="bg-gradient-to-r from-primary-500 to-purple-600 text-white rounded-xl p-8 mb-8">
        <div className="flex flex-col md:flex-row md:items-end space-y-6 md:space-y-0 md:space-x-8">
          {/* Cover Art */}
          <div className="w-48 h-48 bg-white bg-opacity-20 rounded-xl flex items-center justify-center flex-shrink-0">
            <Music className="w-24 h-24" />
          </div>

          {/* Info */}
          <div className="flex-1">
            <div className="flex items-center space-x-2 mb-2">
              <span className="text-sm bg-white bg-opacity-20 px-2 py-1 rounded">
                {playlist.isPublic ? 'Public Playlist' : 'Private Playlist'}
              </span>
              {isOwner && (
                <span className="text-sm bg-green-500 px-2 py-1 rounded">Your Playlist</span>
              )}
            </div>

            <h1 className="text-4xl md:text-5xl font-bold mb-4">{playlist.name}</h1>
            
            {playlist.description && (
              <p className="text-primary-100 mb-4">{playlist.description}</p>
            )}

            {playlist.moodPrompt && (
              <div className="mb-4">
                <span className="text-sm text-primary-200">Generated from mood:</span>
                <p className="text-lg italic">"{playlist.moodPrompt}"</p>
              </div>
            )}

            <div className="flex items-center space-x-6 text-sm">
              <div className="flex items-center space-x-1">
                <User className="w-4 h-4" />
                <span>{playlist.creator?.firstName || 'Anonymous'}</span>
              </div>
              <div className="flex items-center space-x-1">
                <Music className="w-4 h-4" />
                <span>{playlist.tracks?.length || 0} tracks</span>
              </div>
              <div className="flex items-center space-x-1">
                <Clock className="w-4 h-4" />
                <span>{getTotalDuration()}</span>
              </div>
              <div className="flex items-center space-x-1">
                <Heart className="w-4 h-4" />
                <span>{playlist.likes?.length || 0} likes</span>
              </div>
              <div className="flex items-center space-x-1">
                <Calendar className="w-4 h-4" />
                <span>{new Date(playlist.createdAt).toLocaleDateString()}</span>
              </div>
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex items-center justify-between mt-8">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => playlist.tracks?.[0] && playTrack(playlist.tracks[0])}
              className="flex items-center space-x-2 bg-green-500 text-white px-8 py-3 rounded-full font-semibold hover:bg-green-600 transition-colors"
            >
              <Play className="w-5 h-5" />
              <span>Play</span>
            </button>

            <button
              onClick={likePlaylist}
              className={`w-12 h-12 rounded-full flex items-center justify-center transition-colors ${
                playlist.userLiked 
                  ? 'bg-red-500 text-white' 
                  : 'bg-white bg-opacity-20 hover:bg-opacity-30'
              }`}
            >
              <Heart className={`w-6 h-6 ${playlist.userLiked ? 'fill-current' : ''}`} />
            </button>

            <button
              onClick={() => setShowShareModal(true)}
              className="w-12 h-12 bg-white bg-opacity-20 rounded-full flex items-center justify-center hover:bg-opacity-30 transition-colors"
            >
              <Share2 className="w-6 h-6" />
            </button>

            <button
              className="w-12 h-12 bg-white bg-opacity-20 rounded-full flex items-center justify-center hover:bg-opacity-30 transition-colors"
            >
              <Download className="w-6 h-6" />
            </button>
          </div>

          {isOwner && (
            <div className="relative">
              <button
                onClick={() => setShowMenu(!showMenu)}
                className="w-12 h-12 bg-white bg-opacity-20 rounded-full flex items-center justify-center hover:bg-opacity-30 transition-colors"
              >
                <MoreVertical className="w-6 h-6" />
              </button>

              {showMenu && (
                <div className="absolute right-0 top-full mt-2 bg-white rounded-lg shadow-lg py-2 min-w-[180px] z-10">
                  <button
                    onClick={togglePublic}
                    className="w-full px-4 py-2 text-left text-gray-700 hover:bg-gray-100 flex items-center space-x-2"
                  >
                    <Share2 className="w-4 h-4" />
                    <span>Make {playlist.isPublic ? 'Private' : 'Public'}</span>
                  </button>
                  
                  <button className="w-full px-4 py-2 text-left text-gray-700 hover:bg-gray-100 flex items-center space-x-2">
                    <Edit3 className="w-4 h-4" />
                    <span>Edit Details</span>
                  </button>
                  
                  <button
                    onClick={deletePlaylist}
                    className="w-full px-4 py-2 text-left text-red-600 hover:bg-red-50 flex items-center space-x-2"
                  >
                    <Trash2 className="w-4 h-4" />
                    <span>Delete Playlist</span>
                  </button>
                </div>
              )}
            </div>
          )}
        </div>
      </div>

      {/* Audio Features */}
      {playlist.audioFeatures && (
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Audio Profile</h3>
          <div className="grid grid-cols-3 gap-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-primary-600 mb-1">
                {playlist.audioFeatures.energyLevel}%
              </div>
              <div className="text-sm text-gray-600">Energy</div>
              <div className="w-full bg-gray-200 rounded-full h-2 mt-2">
                <div 
                  className="bg-primary-600 h-2 rounded-full" 
                  style={{ width: `${playlist.audioFeatures.energyLevel}%` }}
                ></div>
              </div>
            </div>
            
            <div className="text-center">
              <div className="text-3xl font-bold text-purple-600 mb-1">
                {playlist.audioFeatures.danceability}%
              </div>
              <div className="text-sm text-gray-600">Danceability</div>
              <div className="w-full bg-gray-200 rounded-full h-2 mt-2">
                <div 
                  className="bg-purple-600 h-2 rounded-full" 
                  style={{ width: `${playlist.audioFeatures.danceability}%` }}
                ></div>
              </div>
            </div>
            
            <div className="text-center">
              <div className="text-3xl font-bold text-green-600 mb-1">
                {playlist.audioFeatures.valence}%
              </div>
              <div className="text-sm text-gray-600">Positivity</div>
              <div className="w-full bg-gray-200 rounded-full h-2 mt-2">
                <div 
                  className="bg-green-600 h-2 rounded-full" 
                  style={{ width: `${playlist.audioFeatures.valence}%` }}
                ></div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Tracks List */}
      <div className="bg-white rounded-lg shadow-sm">
        <div className="p-6 border-b border-gray-200">
          <h3 className="text-lg font-semibold text-gray-900">Tracks</h3>
        </div>

        <div className="divide-y divide-gray-100">
          {playlist.tracks?.map((track, index) => (
            <div 
              key={track.id || index} 
              className={`p-4 hover:bg-gray-50 transition-colors group ${
                currentTrack?.id === track.id ? 'bg-primary-50' : ''
              }`}
            >
              <div className="flex items-center space-x-4">
                {/* Track Number / Play Button */}
                <div className="w-8 h-8 flex items-center justify-center">
                  {currentTrack?.id === track.id && isPlaying ? (
                    <button
                      onClick={() => setIsPlaying(false)}
                      className="w-8 h-8 bg-primary-600 text-white rounded-full flex items-center justify-center"
                    >
                      <Pause className="w-4 h-4" />
                    </button>
                  ) : (
                    <button
                      onClick={() => playTrack(track)}
                      className="w-8 h-8 bg-primary-600 text-white rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
                    >
                      <Play className="w-4 h-4 ml-0.5" />
                    </button>
                  )}
                  <span className={`text-sm font-medium text-gray-500 group-hover:hidden ${
                    currentTrack?.id === track.id ? 'hidden' : ''
                  }`}>
                    {index + 1}
                  </span>
                </div>

                {/* Album Art */}
                {track.album?.images?.[0] && (
                  <img 
                    src={track.album.images[0].url} 
                    alt="Album cover"
                    className="w-12 h-12 rounded"
                  />
                )}

                {/* Track Info */}
                <div className="flex-1 min-w-0">
                  <h4 className="font-medium text-gray-900 truncate">{track.name}</h4>
                  <p className="text-sm text-gray-600 truncate">
                    {track.artists?.map(artist => artist.name).join(', ')}
                  </p>
                </div>

                {/* Album */}
                <div className="hidden md:block flex-1 min-w-0">
                  <p className="text-sm text-gray-600 truncate">
                    {track.album?.name}
                  </p>
                </div>

                {/* Duration */}
                <div className="text-sm text-gray-500 w-12 text-right">
                  {Math.floor((track.duration_ms || 0) / 60000)}:{String(Math.floor(((track.duration_ms || 0) % 60000) / 1000)).padStart(2, '0')}
                </div>

                {/* Actions */}
                <div className="flex items-center space-x-2 opacity-0 group-hover:opacity-100 transition-opacity">
                  {track.preview_url && (
                    <button className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center hover:bg-gray-200 transition-colors">
                      <Volume2 className="w-4 h-4 text-gray-600" />
                    </button>
                  )}
                  
                  {track.external_urls?.spotify && (
                    <a
                      href={track.external_urls.spotify}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center hover:bg-green-200 transition-colors"
                    >
                      <ExternalLink className="w-4 h-4 text-green-600" />
                    </a>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Share Modal */}
      {showShareModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-xl p-6 max-w-md w-full mx-4">
            <h3 className="text-xl font-semibold text-gray-900 mb-4">Share Playlist</h3>
            
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Share Link
                </label>
                <div className="flex">
                  <input
                    type="text"
                    value={window.location.href}
                    readOnly
                    className="flex-1 px-3 py-2 border border-gray-300 rounded-l-lg bg-gray-50 text-sm"
                  />
                  <button
                    onClick={copyShareLink}
                    className="px-4 py-2 bg-primary-600 text-white rounded-r-lg hover:bg-primary-700 transition-colors"
                  >
                    <Copy className="w-4 h-4" />
                  </button>
                </div>
              </div>
            </div>

            <div className="flex justify-end space-x-3 mt-6">
              <button
                onClick={() => setShowShareModal(false)}
                className="px-4 py-2 text-gray-600 hover:text-gray-800 transition-colors"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PlaylistDetail;